.form-container {
  margin: 4rem 6rem;
  color: 2a2a2a;
}

.form-container form {
  padding-top: 3rem;
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: auto;
}

.form-container h5 {
  text-align: left;
}

.form-container input {
  height: 3rem;
  padding: 0 1rem;
  margin-bottom: 2rem;
  border-radius: .3rem;
  border: 1px solid #2a2a2a;
}

.form-container textarea {
  padding: 1rem;
  margin-bottom: 2rem;
  border-radius: .3rem;
  border: 1px solid #2a2a2a;
}

.form-container button {
  display: inline;
}